
    <template>
      <section class="market-doc-container">
        <h2>DatePicker 日期选择器</h2>
<h3>基本使用</h3>
<mku-example>
        
        <template slot="source"><element-demo0 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;div&gt;
    &lt;mku-date-picker
      v-model=&quot;value&quot;
      placeholder=&quot;请选择日期&quot;
    /&gt;
    &lt;mku-date-picker
      v-model=&quot;value2&quot;
      placeholder=&quot;请选择日期&quot;
    /&gt;
  &lt;/div&gt;
&lt;/template&gt;

&lt;script&gt;
  export default {
    data () {
      return {
        value: '',
        value2: new Date(2019, 11, 12)
      }
    }
  }
&lt;/script&gt;
</code></pre></template></mku-example><h3>日期范围</h3>
<mku-example>
        
        <template slot="source"><element-demo1 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;div&gt;
    &lt;mku-date-picker
      type=&quot;date-range&quot;
      v-model=&quot;value1&quot;
      format-value=&quot;YYYY-MM-DD&quot;
      placeholder=&quot;请选择日期范围&quot;
    /&gt;
    &lt;mku-date-picker
      type=&quot;date-range&quot;
      v-model=&quot;value2&quot;
      format-value=&quot;YYYY-MM-DD&quot;
      placeholder=&quot;请选择日期范围&quot;
    /&gt;
  &lt;/div&gt;
&lt;/template&gt;

&lt;script&gt;
  export default {
    data () {
      return {
        value1: '',
        value2: [new Date(2019, 12, 12), new Date(2019, 12, 20)],
      }
    }
  }
&lt;/script&gt;
</code></pre></template></mku-example><h3>日期时间选择器</h3>
<mku-example>
        
        <template slot="source"><element-demo2 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;div&gt;
    &lt;mku-date-picker
      type=&quot;datetime&quot;
      :value=&quot;value1&quot;
      placeholder=&quot;选择日期时间&quot;
    /&gt;
    &lt;mku-date-picker
      type=&quot;datetime&quot;
      v-model=&quot;value2&quot;
      placeholder=&quot;选择日期时间&quot;
    /&gt;
    &lt;mku-date-picker
      type=&quot;datetime-range&quot;
      v-model=&quot;value3&quot;
      format-value=&quot;YYYY-MM-DD HH:mm:ss&quot;
      placeholder=&quot;选择日期时间&quot;
      unlink-panels
    /&gt;
  &lt;/div&gt;
&lt;/template&gt;

&lt;script&gt;
  export default {
    data () {
      return {
        value1: '',
        value2: Date.now(),
        value3: [],
      }
    }
  }
&lt;/script&gt;
</code></pre></template></mku-example><h3>面板联动和非联动</h3>
<mku-example>
        
        <template slot="source"><element-demo3 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;div&gt;
    &lt;mku-date-picker
      type=&quot;date-range&quot;
      v-model=&quot;value1&quot;
      format-value=&quot;YYYY-MM-DD&quot;
      placeholder=&quot;默认联动&quot;
    /&gt;
    &lt;mku-date-picker
      type=&quot;date-range&quot;
      v-model=&quot;value2&quot;
      format-value=&quot;YYYY-MM-DD&quot;
      placeholder=&quot;支持不联动&quot;
      unlink-panels
    /&gt;
  &lt;/div&gt;
&lt;/template&gt;

&lt;script&gt;
  export default {
    data () {
      return {
        value1: [],
        value2: [],
      }
    }
  }
&lt;/script&gt;
</code></pre></template></mku-example><h3>只读和禁用</h3>
<p>只读模式下，输入框不可以手动编辑。而禁用之后，整个日期组件不可以操作。</p>
<mku-example>
        
        <template slot="source"><element-demo4 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;div&gt;
    &lt;mku-date-picker
      v-model=&quot;value1&quot;
      readonly
      placeholder=&quot;请选择时间&quot;
    /&gt;
    &lt;mku-date-picker
      v-model=&quot;value2&quot;
      disabled
      placeholder=&quot;请选择时间&quot;
    /&gt;
  &lt;/div&gt;
&lt;/template&gt;

&lt;script&gt;
  export default {
    data () {
      return {
        value1: [],
        value2: [],
      }
    }
  }
&lt;/script&gt;
</code></pre></template></mku-example><h3>Date-Picker Attributes</h3>
<table>
<thead>
<tr>
<th style="text-align:left">参数</th>
<th style="text-align:left">说明</th>
<th style="text-align:center">类型</th>
<th style="text-align:center">可选值</th>
<th style="text-align:left">默认值</th>
<th style="text-align:left">必填</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">value</td>
<td style="text-align:left">绑定的值，可使用<code>v-model</code>双向绑定。<br>为Date类型时，例如<code>new Date()</code>；<br>也可以为包含2个Date类型、String类型或者时间戳的数组，eg：<code>['2019-12-22 12:22:14', Date.now()]</code>；<br>为String类型时，例如<code>'2019-12-22 12:22:14'</code>，组件会自动解析其中的日期参数，完整的参数请查阅<code>Date Attributes</code>。</td>
<td style="text-align:center"><code>Date | String | Number | Array</code></td>
<td style="text-align:center">-</td>
<td style="text-align:left">-</td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">type</td>
<td style="text-align:left">类型，支持选择日期、日期时间、日期范围、日期时间范围。</td>
<td style="text-align:center"><code>String</code></td>
<td style="text-align:center"><code>date | datetime | date-range | datetime-range</code></td>
<td style="text-align:left"><code>time</code></td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">formatText</td>
<td style="text-align:left">格式化时间，仅用于input上展示。<code>date | date-range</code>类型时默认为&quot;YYYY:MM:DD&quot;; <code>datetime | datetime-range</code>类型时默认为&quot;YYYY:MM:DD HH:mm:ss&quot;;</td>
<td style="text-align:center"><code>String</code></td>
<td style="text-align:center">-</td>
<td style="text-align:left">-</td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">formatValue</td>
<td style="text-align:left">格式化时间，用于输出，默认为Date对象。例如change事件的回调参数</td>
<td style="text-align:center"><code>String</code></td>
<td style="text-align:center">-</td>
<td style="text-align:left"><code>HH:mm:ss</code></td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">placeholder</td>
<td style="text-align:left">文本提示占位符</td>
<td style="text-align:center"><code>String</code></td>
<td style="text-align:center">-</td>
<td style="text-align:left">-</td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">separator</td>
<td style="text-align:left">范围选择时，开始和结束事件直接的连接文本</td>
<td style="text-align:center"><code>String</code></td>
<td style="text-align:center">-</td>
<td style="text-align:left"><code>～</code></td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">clearable</td>
<td style="text-align:left">可以清除</td>
<td style="text-align:center"><code>Boolean</code></td>
<td style="text-align:center">-</td>
<td style="text-align:left"><code>true</code></td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">readonly</td>
<td style="text-align:left">只读</td>
<td style="text-align:center"><code>Boolean</code></td>
<td style="text-align:center">-</td>
<td style="text-align:left"><code>false</code></td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">disabled</td>
<td style="text-align:left">禁用</td>
<td style="text-align:center"><code>Boolean</code></td>
<td style="text-align:center">-</td>
<td style="text-align:left"><code>false</code></td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">placement</td>
<td style="text-align:left">面板位置</td>
<td style="text-align:center"><code>String</code></td>
<td style="text-align:center"><code>top | top-start | top-end | right | right-start | right-end | bottom | bottom-start | bottom-end | left | left-start | left-end</code></td>
<td style="text-align:left"><code>bottom-start</code></td>
<td style="text-align:left">否</td>
</tr>
</tbody>
</table>
<h3>Date Attributes</h3>
<table>
<thead>
<tr>
<th style="text-align:center">参数</th>
<th style="text-align:left">说明</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:center">HH</td>
<td style="text-align:left">小时，24时制，2位展示。即小于10时会在前面自动补0</td>
</tr>
<tr>
<td style="text-align:center">H</td>
<td style="text-align:left">小时，24时制，1位展示</td>
</tr>
<tr>
<td style="text-align:center">hh</td>
<td style="text-align:left">小时，12时制，2位展示</td>
</tr>
<tr>
<td style="text-align:center">h</td>
<td style="text-align:left">小时，12时制，1位展示</td>
</tr>
<tr>
<td style="text-align:center">mm</td>
<td style="text-align:left">分钟，2位展示</td>
</tr>
<tr>
<td style="text-align:center">m</td>
<td style="text-align:left">分钟，1位展示</td>
</tr>
<tr>
<td style="text-align:center">ss</td>
<td style="text-align:left">秒，2位展示</td>
</tr>
<tr>
<td style="text-align:center">s</td>
<td style="text-align:left">秒，1位展示</td>
</tr>
</tbody>
</table>
<h3>Date-Picker Events</h3>
<table>
<thead>
<tr>
<th style="text-align:left">事件名</th>
<th style="text-align:left">说明</th>
<th style="text-align:left">回调函数参数</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">change</td>
<td style="text-align:left">value绑定值发生变化时触发，即v-model时生效</td>
<td style="text-align:left">format-vale格式化后的当前选择的时间</td>
</tr>
<tr>
<td style="text-align:left">confirm</td>
<td style="text-align:left">点击确认按钮时触发</td>
<td style="text-align:left">format-vale格式化后的当前选择的时间</td>
</tr>
<tr>
<td style="text-align:left">clear</td>
<td style="text-align:left">通过输入框的清除按钮或底部的清除按钮清空时触发</td>
<td style="text-align:left">-</td>
</tr>
</tbody>
</table>

